	module.exports = {"data":{"markdownRemark":{"html":"<p>Comments are a great way to leave notes to yourself and to other people who will later need to figure out what it does. Any code in it will be ignored.</p>\n<p>Let’s take a look at the two ways you can write comments in JavaScript.</p>\n<ul>\n<li>The double-slash comment will comment out the remainder of the text on the current line:</li>\n</ul>\n<p><code>// This is a comment.</code></p>\n<ul>\n<li>The slash-star-star-slash comment will comment out everything between the <code>/*</code> and the <code>*/</code> characters:</li>\n</ul>\n<p><code>/* This is also a comment */</code></p>","fields":{"slug":"/javascript/tutorials/comment-your-javascript-code/"},"frontmatter":{"title":"Comment Your JavaScript Code"}}},"pathContext":{"slug":"/javascript/tutorials/comment-your-javascript-code/"}}

